@import 'src/Components/Common/Styles/Sass/Variants';

.container{
    background: $primary-color;
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
    .contactBlock{
        display: flex;
        justify-content: space-evenly;
        a {
            width: 40%;
            img {
                width: 8%;
                margin-bottom: 10px;
                height: auto;
                @media (max-width: 1129px) {
                    width: 30%;
                }
            }
        }
    }
}